    crabFire: function () {
        setTimeout(function () {
            var crabSvg = document.getElementById('crabSvg'),
                crabRocketSvg = document.getElementById('crabRocketSvg'),
                fireTarget = document.getElementById('photo').getElementsByClassName('separator')[0],
                shift = fireTarget.getBoundingClientRect().top - crabRocketSvg.getBoundingClientRect().top,
                rubCount = 15,
                rubShiftMin = 100,
                rubShiftMax = 400,
                rubSet = [];


            crabSvg.classList.add('crab-fire-prepare');
            crabSvg.classList.add('shake-rotate');

            setTimeout(function () {
                crabSvg.classList.remove('crab-fire-prepare');
                crabSvg.classList.remove('shake-rotate');
                crabSvg.classList.add('crab-fire-start');

                crabRocketSvg.style.transform = 'translateY(' + shift + 'px)';

                setTimeout(function () {
                    crabRocketSvg.classList.add('bang')
                    fireTarget.getElementsByTagName('div')[0].style.opacity = 0;

                    for(var i = 0; i < rubCount; i++) {
                        rubSet.push( document.createElement('div') );
                        rubSet[i].classList.add('rub');
                    }
                    for(var j = 0; j < rubCount; j++) {
                        fireTarget.appendChild( rubSet[j] );
                    }
                    setTimeout(function () {
                        for(var k = 0; k < rubCount; k++) {
                            rubSet[k].classList.add('go');
                        }

                        crabSvg.classList.add('animation-done');
                        setTimeout(function () {
                            crabSvg.classList.remove('animation-done');
                        }, 1000)
                    }, 100)

                }, 1500)

            }, 700)
        }, 2650)
    },

    loadCanvasImg: function(wrapper){
        var el = wrapper.getElementsByClassName('canvas-container')[0];

        if(el.dataset.preloaded){
            return
        }else{
            var preloader = new canvasPreloader( el );
            preloader.startHeight = 1;
            preloader.startWidth = 2;
            preloader.init();
            el.setAttribute('data-preloaded', 1);
        }
    },

