var sweeterComputed = {
    logoSvg: function(){
        var logo = document.getElementById('headerLogo'),
            svg = logo.getElementsByTagName('svg')[0];
        return svg.outerHTML;
    },
    sweeterSvg: function(){
        return document.getElementById('headerSweeter').innerHTML;
    },
    pocketImg: function(){
        var img = new Image;
        img.src = this.images.folder + this.images.pocket;
        return img.outerHTML;
    },
    currentCoffee: function () {
        return this.coffees[this.activeCoffee];
    },
    currentShare: function () {
        return this.sharesSlides[this.activeShare];
    },
    currentPhoto: function () {
        return this.photoSlides[this.activePhoto];
    },

};