var sweeterWatchers = {

    activeCoffee: function(newActive){
        if(!this.coffeeSliderIs) {
            this.movePocket(newActive);
            return;
        }
    },
    browserWidth: function (val) {
        if( val > this.coffeeSliderToggle ) {
            this.coffeeSliderIs = 0;
            return;
        }
        this.coffeeSliderIs = 1;
    },
    coffeeSliderIs: function (val) {
        if ( val ) {
            this.initCoffeeSlider();
            return;
        }
        this.destroyCoffeeSlider();
    },
    activeNav: function (index) {
        this.setPointerToActive();
    }
};