"use strict";
function canvasPreloader(el) {

    this.startWidth = 1;
    this.startHeight = 1;
    this.paintDelay = 70;
    this.size = {
        x: 0,
        y: 0
    };

    var wrapper = el,
        canvas = document.createElement("canvas"),
        context = canvas.getContext('2d'),
        tempCanvas = document.createElement("canvas"),
        tempContext = tempCanvas.getContext('2d'),
        precision = 15,
        blocksH = 0,
        blocksW = 0,
        height = wrapper.offsetHeight,
        width = wrapper.offsetWidth,
        _this = this;

    function initImage() {
        var fullImage = new Image();
        fullImage.src = wrapper.dataset.src;

        fullImage.onload = function () {
            drawingHiddenImage(fullImage);
            _this.fullImage = fullImage;
        };
    }

    function getAverageImageColor(ctx, x, y, width, height) {
        var imageData = ctx.getImageData(x, y, width, height),
            data = imageData.data,
            rgb = { r: 0, g: 0, b: 0 },
            count = 0;

        if (precision > width || precision > height) {
            precision = 1;
        }

        for (var i = 0; i < data.length; i += 4 * precision) {
            rgb.r += data[i];
            rgb.g += data[i + 1];
            rgb.b += data[i + 2];
            count++;
        }

        rgb.r = ~~(rgb.r / count);
        rgb.g = ~~(rgb.g / count);
        rgb.b = ~~(rgb.b / count);

        return rgb;
    }

    function initCanvas() {
        context.height = tempContext.height = canvas.height = tempCanvas.height = height;
        context.width = tempContext.width = canvas.width = tempCanvas.width = width;

        wrapper.appendChild(canvas);
        wrapper.appendChild(tempCanvas);

        tempCanvas.classList.add('temp-canvas');

        initImage();
    }

    function drawingHiddenImage(image) {
        if (wrapper.dataset.crop){
            var x = (tempContext.width  - image.width ) * 0.5,   // this = image loaded
                y = (tempContext.height - image.height) * 0.5;
            tempContext.drawImage(image, x, y, image.width, tempContext.height);
        }else{
            tempContext.drawImage(image, 0, 0, tempContext.width, tempContext.height);
        }

        var paintTimer = setTimeout(function repaint() {

            blocksPainting();

            //console.log(_this.size.y);

            if (_this.size.x < width && _this.size.y < height) {
                setTimeout(repaint, _this.paintDelay);
            } else {
                paintImage();
            }
        }, _this.paintDelay);
    }

    function paintImage() {
        context.filter = 'blur(5px)';
        if (wrapper.dataset.crop) {
            var x = (context.width  - _this.fullImage.width ) * 0.5,   // this = image loaded
                y = (context.height - _this.fullImage.height) * 0.5;

            context.drawImage(_this.fullImage, x, y, _this.fullImage.width, _this.fullImage.height);
            setTimeout(function () {
                context.globalAlpha = 1;
                context.filter = 'none';
                context.drawImage(_this.fullImage, x, y, _this.fullImage.width, _this.fullImage.height);
            }, _this.paintDelay);
        } else {
            context.drawImage(_this.fullImage, 0, 0, context.width, context.height);
            setTimeout(function () {
                context.globalAlpha = 1;
                context.filter = 'none';
                context.drawImage(_this.fullImage, 0, 0, context.width, context.height);
            }, _this.paintDelay);
        }
    }

    function blocksPainting() {
        var blocksW = _this.size.x || _this.startWidth,
            blocksH = _this.size.y || _this.startHeight,
            sectionWidth = width / blocksW,
            sectionHeight = height / blocksH,
            color = void 0;

        if (sectionWidth > precision && sectionHeight > precision) {

            for (var i = 0; i < blocksH; i++) {
                for (var j = 0; j < blocksW; j++) {
                    color = getAverageImageColor(tempContext, j * sectionWidth, i * sectionHeight, sectionWidth, sectionHeight);
                    paintBlock(color, context, j * sectionWidth, i * sectionHeight, sectionWidth, sectionHeight);
                }
            }

            _this.size.x = blocksW * 2;
            _this.size.y = blocksH * 2;
        } else {
            _this.size.x = width;
            _this.size.y = height;
        }
    }

    function rgbToHex(r, g, b) {
        return "#" + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
    }

    function paintBlock(color, ctx, x, y, blockWidth, blockHeight) {
        ctx.fillStyle = rgbToHex(color.r, color.g, color.b);
        ctx.globalAlpha = 0.2;
        ctx.fillRect(x, y, blockWidth, blockHeight);
    }

    this.init = function () {
        initCanvas();
    };
}
