var sweeterVue = new Vue({
    el: '#sweeter',
    data: sweeterData,
    computed: sweeterComputed,
    methods: sweeterMethods,
    watch: sweeterWatchers,
    mounted: function(){
        var homeSection = document.getElementById('home'),
            _this = this;

            window.addEventListener('scroll', this.scrollHandler);
            homeSection.addEventListener('mousemove', this.moveBg);

        this.sharesSwiper = new Swiper ('#sharesSlider', {
            // Optional parameters
            loop: true,
            speed: 600,
            // Navigation arrows
            nextButton: '.share-next',
            prevButton: '.share-prev',
            onSlideChangeStart: function(swiper){
                _this.slidePreloadHandler(swiper);
                setTimeout(function () {
                    sweeterVue.activeShare = swiper.realIndex;
                }, 200)
            },

        });
        this.photoSwiper = new Swiper ('#photoSlider', {
            // Optional parameters
            loop: true,
            speed: 600,
            // Navigation arrows
            nextButton: '.photo-next',
            prevButton: '.photo-prev',
            onSlideChangeStart: function(swiper){
                _this.slidePreloadHandler(swiper);
                setTimeout(function () {
                    sweeterVue.activePhoto = swiper.realIndex
                }, 200)
            }
        });
        this.footerSwiper = new Swiper ('#footerSlider', {
            // Optional parameters
            loop: true,
            speed: 600,
            // Navigation arrows
            nextButton: '.footer-next',
            prevButton: '.footer-prev',
            onSlideChangeStart: function(swiper){
                _this.slidePreloadHandler(swiper);
            }
        });



        this.activeCoffee = 0;
        this.activeShare = 0;
        this.activePhoto = 0;

        this.onLoaded();
    },
    created: function () {
        this.setSize();
        window.addEventListener('resize', this.setSize);
    }
});
